.digital-dentistry {
  padding: 0px 100px 100px;
  border-left: 4px solid #ba9e66;
  .digital-dentistry-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    .digital-dentistry-header {
      // font-size: 38px;
      padding: 100px 50px 20px;
      font-size: 52px;
      font-weight: 400;
      span {
        color: #ba9e66;
      }
    }
    .digital-dentistry-container {
      display: flex;
      flex-direction: column;
      gap: 10px;
      // border-left: 2px solid #ba9e66;
      // border-right: 2px solid #ba9e66;
      .digital-dentistry-desc {
        padding: 30px 100px;
        border-bottom: 2px solid #ba9e66;
        .digital-dentistry-description {
          // font-family: "Proza Libre", sans-serif;

          font-size: 24px;
          line-height: 2;
          text-align: center;
          letter-spacing: 0.5px;
          font-weight: 400;
          // color: rgb(61, 61, 60);
          color: #2d2d2d;
        }
        h2 {
          font-size: 42px;
          padding: 0;
          text-align: center;
          position: relative;
        }
        h2::after {
          content: " ";
          height: 2px;
          width: 14%;
          background: #000;
          display: block;
          position: absolute;
          top: 50%;
          right: 0;
        }
        h2::before {
          content: " ";
          height: 2px;
          width: 14%;
          background: #000;
          display: block;
          position: absolute;
          top: 50%;
          left: 0;
        }
      }
    }
  }
}
@media (max-width: 1366px) {
  .digital-dentistry {
    padding: 0 80px 80px;
    .digital-dentistry-wrap {
      .digital-dentistry-header {
        padding: 80px 40px 10px !important;
        font-size: 45px !important;
      }
      .digital-dentistry-container {
        .digital-dentistry-desc {
          padding: 20px 80px !important;
        }
      }
    }
  }
  .digital-dentistry-description {
    font-size: 22px !important;
    h2 {
      font-size: 38px !important;
    }
  }
}
